import React from 'react';
import '../styles/Projects.css';

const Projects = () => {
  const projects = [
    {
      id: 1,
      title: 'Stock Review Tool',
      description: 'Developed a stock analysis tool using Python and React, leveraging the yfinance library to extract data from Yahoo Finance. Built a user-friendly interface for exploring company profiles, historical prices, financial statements, and key metrics. The application enables users to make informed investment decisions through in-depth analysis and interactive charts.',
      technologies: ['React.js', 'Python', 'yfinance', 'API Integration', 'Data Extraction'],
      image: 'stock-review.jpg',
      link: '#'
    },
    {
      id: 2,
      title: 'Interactive Decision-Making Story App',
      description: 'Created a single-page application using React to deliver a personalized, decision-driven narrative experience. Implemented a JSON graph structure to represent the branching storyline, allowing users to make choices that directly impact the narrative\'s progression. Designed a responsive and engaging user interface, utilizing React components and state management for seamless user interaction.',
      technologies: ['React.js', 'JSON', 'State Management', 'UI/UX Design'],
      image: 'story-app.jpg',
      link: '#'
    }
  ];

  return (
    <div className="projects-container">
      <h1>My Projects</h1>
      <div className="projects-grid">
        {projects.map((project) => (
          <div className="project-card" key={project.id}>
            <div className="project-image">
              <img src={`/images/${project.image}`} alt={project.title} />
            </div>
            <div className="project-info">
              <h2>{project.title}</h2>
              <p>{project.description}</p>
              <div className="project-tech">
                {project.technologies.map((tech, index) => (
                  <span key={index} className="tech-tag">
                    {tech}
                  </span>
                ))}
              </div>
              <a href={project.link} className="btn btn-primary" target="_blank" rel="noopener noreferrer">
                View Project
              </a>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Projects;